							<td><span id="tips_name"></span></td>
						</tr>
						<tr>
							<td class="tr">地址信息:&nbsp;&nbsp;</td>
							<td>
								<!--2.确定事件，通过函数传参的方式拿到改变后的城市-->
								<select id="province">
									<option>--请选择--</option>
									<option value="0">北京</option>
									<option value="1">湖北</option>
									<option value="2">湖南</option>
									<option value="3">河北</option>
									<option value="4">河南</option>
									<option value="5">上海</option>
									<option value="6">广东</option>
									<option value="7">浙江</option>
									<option value="8">天津</option>
									<option value="9">重庆</option>
<!-- 									<option value="10"></option>
									<option value="11"></option>
									<option value="12"></option> -->
								</select>
								<select id="city">

								</select>
							</td>
						</tr>
						<tr>
							<td class="tr">*详细地址:&nbsp;&nbsp;</td>
							<td><textarea  rows="3" cols="53" placeholder="请输入详细地址信息,如道路、门牌号、小区、楼栋号、单元等信息"></textarea></td>
						</tr>
						<tr>
							<td class="tr">邮政编码:&nbsp;&nbsp;</td>
							<td><input type="text" placeholder="请填写邮编" name=""></td>
						</tr>
							<td class="tr">*手机号码:&nbsp;&nbsp;</td>
							<td><input type="text" placeholder="长度不超过11个字符" id="c_phone" onblur="return checkForm()" name=""></td>
							<td><span id="tips_phone"></span></td>
						</tr>
					</table>
					<br>
						<input class="cart-input" type="submit" value="使用" name="">
				</form>
