	text-align: right;

}

.cart-table input {
	height: 32px;
	width: 337px;
	border: 1px solid #424242;
}

tr td textarea {
	/*height: 48px;
	width: 336px;*/
	border: 1px solid #424242;
	resize: none;
	line-height: 1.2;
}

